  computed: {
    curApprovalData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.approvalData.length ? this.approvalData.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.approvalData[i])
      }
      return result
    }
  },
  methods: {
    resetForm () {
      this.$refs['agencyForm'].resetFields()
      this.agency.status = []
    },
    query () {
      this.currentPage = 1
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    },
    detail () {
      this.$router.push({path: '/admin/source/query/detail'})
    },
    change (val) {
      this.currentPage = 1
      if (val === 1) {
        this.bizShow = true
        this.columns = this.columns1
        this.approvalData = this.approvalData1
      } else if (val === 2) {
        this.bizShow = false
        this.columns = this.columns2
        this.approvalData = this.approvalData2
      } else {
